@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  overflow-x: hidden;
}

section {
  height: 200vh;
  width: 100vw;
  text-align: center;
  overflow: hidden;
}

.slide-1 {
  /* background-image: linear-gradient( 134.6deg,  rgba(201,37,107,1) 15.4%, rgba(116,16,124,1) 74.7% ); */
  background-image: radial-gradient(circle farthest-corner at 83.7% 4.3%, rgba(173, 0, 171, 1) 0%, rgba(15, 51, 92, 1) 90%);
}

.slide-1-wave-container {
  position: absolute;
  left: -2px;
  width: 101%;
  top: -22vw;
  -webkit-filter: drop-shadow( 0px -9px 43px 0px rgba(0, 0, 0, 0.93));
          filter: drop-shadow( 0px -9px 43px 0px rgba(0, 0, 0, 0.93));
}


@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%) scale(.9);
            transform: translateY(-10%) scale(.9);
  }

  20% {
    opacity: 0;
    -webkit-transform: translateY(-2%) scale(.9);
            transform: translateY(-2%) scale(.9);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}


@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%) scale(.9);
            transform: translateY(-10%) scale(.9);
  }

  20% {
    opacity: 0;
    -webkit-transform: translateY(-2%) scale(.9);
            transform: translateY(-2%) scale(.9);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

.main-title {
  color: white;
  font-weight: 900;
  font-size: 82px;
  text-transform: uppercase;
  /* line-height: 70vh; */
  margin-top: 25%;
  margin-bottom: 0;
  font-family: 'Raleway', sans-serif;
  text-shadow: 0px 4px 18px rgba(0, 0, 0, 0.253);

  -webkit-animation: fadeInAnimation cubic-bezier(.29, .81, .48, 1.03) 2s;

          animation: fadeInAnimation cubic-bezier(.29, .81, .48, 1.03) 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.subtitle {
  color:white;
  font-size: 30px;
  display: inline-block;
  margin-top: 0;
  font-weight: bold;
  text-shadow: 0px 4px 12px rgba(0, 0, 0, 0.253);
}

.slide-2 {
  background-color: white;
}

.slide-2 {
  background-color: white;
}

.slide-2-statement, .slide-3-statement {
  font-size: 60px;
  /* display: inline-block; */
  margin-bottom: 12vh;
  font-weight: 700;
  font-style: italic ;
}

